<template>
  <div id="app">
    <p class="home">旅者</p>
    <div id="time"></div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'App',
  mounted() {
    showTime();
    function showTime(){
      var time = new Date();
      document.getElementById("time").innerHTML=time.toLocaleString();
    }
    setInterval(showTime,1000);
  }
}
</script>

<style>
  *{
    margin: 0;
    padding: 0;
  }
  html, body {
    width: 100%;
    height: 100%;
    background: url("./assets/images/bg.jpg") no-repeat;
    background-size: 100% 100%;
    margin: 0;
  }
  #time{
    margin:0.2rem 0 0 2rem;
    font-size: 20px;
  }
  .home {
    margin:0.5rem 0 0 2rem;
    font-size: 0.5rem;
    font-family:'宋体';
    font-weight: bold;
  }
  #app {
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
  }
</style>



