<template>
  <div id="index">
    <el-row :gutter="0">
      <el-col :xs="22" :sm="19" :lg="19" :md="23" >
        <el-tabs type="border-card" class="home_list">
          <el-tab-pane label="首页" class="cards">
            <router-link :to="{ name: 'content'}">
              <el-col :xs="22" :sm="21" :lg="11" :md="13">
                <el-card shadow="hover" class="card">
                  <img src="../assets/images/JsLogo.jpg" class="card_i">
                  <p class="card_p">Javascript基础篇</p>
                  <span class="card_sp">
                    一、数据类型简介
                    1、JS数据类型可以分为两种：基本类型与引用类型
                    2、基本类型:字符(String);数值(Number);布尔(Boolean);空(Null);未命名(Undefined);
                    3、引用类型:对象(Object);数组(Array);函数(Function)
                    4、Es6新出一种数据类型：符号(Symbol)
                  </span>
                </el-card>
              </el-col>
            </router-link>

          </el-tab-pane>
          <el-tab-pane label="配置管理">配置管理</el-tab-pane>
          <el-tab-pane label="角色管理">角色管理</el-tab-pane>
          <el-tab-pane label="定时任务补偿">定时任务补偿</el-tab-pane>
        </el-tabs>
      </el-col>

    </el-row>

  </div>
</template>

<script>

  export default {
    name: '',
    data() {
      return {}
    },
  }
</script>

<style scoped>


  .home_list {
    width: 90%;
    margin-left: 10%;
    margin-top: 3%;
  }
  .cards{
    margin-left: 0.3rem;
  }
  .card {
    position: relative;
    height: 2rem;
    margin-top: 0.5rem;
  }
  .card_i {
    float: left;
    height: 1.2rem;
  }
  .card_p{
    float: left;
    padding-left: 0.5rem;
    font-size: 0.3rem;
    font-weight: bold;
    font-family: '宋体';
  }
  .card_sp {
    text-indent:0.51rem;
    width: 6rem;
    height: 1rem;
    float: left;
    margin-top: 0.2rem;
    font-size: 0.25rem;
    font-weight: bold;
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
  }
  @media screen and (max-width:768px) {
    .card{
      height: 1.8rem;
    }
    .card_p{
      float: left;
      margin-left: -0.2rem;
    }
    .card_i {
      height: 1rem;
    }
    .card_sp{
      width: 5rem;
      height: 0.82rem;
      float:left;
      margin-left: 0.36rem;
      overflow : hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
  }

</style>



